import React, { Component } from 'react'
import logo from '../../BottomSection/logo.png'
import {Link, withRouter} from 'react-router-dom'
import { UserContext } from '../../../helpers/dataContext'
import { Redirect } from 'react-router-dom'


class FirstRegform extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: [],
            redirect: false
        }

    }
    static contextType = UserContext;

    emailValidate = (value) => {
        return !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value);
    };

    nameValidate = (value) => {
        return !/^([^0-9]*)$/.test(value);
    };

    saveData = (e) => {
        let form = e.target.parentElement;
        let first_name = form.querySelector('.fname').value.trim();
        let email = form.querySelector('.email').value.trim();

        if(first_name.length === 0) {
            this.setState({
                errors: ['Enter Name']
            });
            return this.state.errors
        }
        else if(email.length === 0) {
            this.setState({
                errors: ['Enter Email']
            });
            return this.state.errors
        } else if(this.nameValidate(first_name)) {
            this.setState({
                errors: ['Please enter name without digits']
            });
            return this.state.errors
        } else if(this.emailValidate(email)) {
            this.setState({
                errors: ['Invalid email format']
            });
            return this.state.errors
        } else {
            this.setState({data: {first_name, email}, redirect: true}, () => {
                this.props.handleForward(this.state.data)
            })
        }
    };

    render() {
        let languageManager = this.props.languageManager();

        if (!this.state.redirect) {
            return (
                <div className="FirstRegform">
                    <img src={logo} alt="logo" className="logo"/>
                    <div className='inner'>
                        <div className='form-wrapper'>
                            {this.state.errors && <div className="errors">
                                {this.state.errors[0]}
                            </div>}
                            <input className="inputfield fname" type="text" name="firstName" defaultValue={this.context.firstName} onChange={(e) => {this.context.getValueFromInputs(e)}} placeholder={languageManager.fname} />
                            <input className="inputfield email" type="text" name="email" defaultValue={this.context.email} onChange={(e) => {this.context.getValueFromInputs(e)}} placeholder={languageManager.email} />
                            <button onClick={this.saveData} className='start'>{languageManager.button}</button>
                        </div>
                    </div>
                </div>
            )
        } else { return <Redirect to={{ pathname: '/members', search: this.props.location.search}}/> }

    }
}
export default withRouter(FirstRegform);
